import React from 'react'
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap'

const style = {
  justifyContent: 'center', marginTop: '20px'
}

export const PageList = () => (
  <Pagination style={style}>
    <PaginationItem disabled>
      <PaginationLink previous href="#"/>
    </PaginationItem>
    <PaginationItem active>
      <PaginationLink href="#">
        1
      </PaginationLink>
    </PaginationItem>
    <PaginationItem>
      <PaginationLink href="#">
        2
      </PaginationLink>
    </PaginationItem>
    <PaginationItem>
      <PaginationLink href="#">
        3
      </PaginationLink>
    </PaginationItem>
    <PaginationItem>
      <PaginationLink href="#">
        4
      </PaginationLink>
    </PaginationItem>
    <PaginationItem>
      <PaginationLink href="#">
        5
      </PaginationLink>
    </PaginationItem>
    <PaginationItem>
      <PaginationLink next href="#"/>
    </PaginationItem>
  </Pagination>
)


